<template>
  <div id="app">
    <div class="header">
      <span>
        {{ actualWeather.date.dayOfWeek }}, {{ actualWeather.date.dayOfMonth }}
        {{ actualWeather.date.month }}
      </span>
      <input type="text" v-model="tempCity" />
      <button @click="chooseCity">OK</button>
    </div>
    <div class="main">
      <div class="board">
        <div class="top">
          <div class="current_weather">
            <span id="temperature">{{ actualWeather.temp }}&deg;</span>
            <span id="weather_type">{{ actualWeather.main }}</span>
            <div class="temperature_info">
              <div class="Humidity">
                <span>Humidity</span>
                <span>{{ actualWeather.humidity }}%</span>
              </div>
              <div class="separator"></div>
              <div class="wind">
                <span>Wind</span>
                <span>{{ actualWeather.wind.speed }}K/H</span>
              </div>
            </div>
          </div>
          <div class="location">
            <span id="location">{{ city }}</span>
            <hr />
          </div>
        </div>
        <div class="middle-top"></div>
        <div class="middle-bottom"></div>
        <div class="bottom">
          <div class="days">
            <div
              class="day"
              @click="changeActualWeather(index)"
              v-for="(wtr, index) in weather"
              :key="index"
            >
              <span>{{ getShortDayName(wtr.date.dayOfWeek) }}</span>
              <img :src="getWeatherImage(wtr.icon)" :alt="wtr.description" />
              <span>{{ wtr.temp }}&deg;</span>
              <span>{{ wtr.description }}</span>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "App",
  data() {
    return {
      weather: [],
      actualWeather: {
        main: "",
        description: "",
        humidity: 0,
        temp: 0,
        date: {
          dayOfWeek: 0,
          dayOfMonth: 0,
          month: "",
        },
        wind: {
          speed: 0,
          degrees: 0,
        },
      },
      city: "Murom",
      tempCity: "",
      
    };
  },
  created() {
    this.fetchData();
  },
  methods: {
    async fetchData() {
      await axios
        .get(
          "https://cors-anywhere.herokuapp.com/https://api.openweathermap.org/data/2.5/forecast?q=" +
            this.city +
            ",RU&type=like&APPID=9483689d13c911ff928ce0a34f41208f"
        )
        .then((response) => {
          this.weather = [];
          let temp = response.data.list.filter((lst) =>
            lst.dt_txt.includes("15:00:00")
          );
          temp.forEach((element) => {
            this.weather.push({
              main: element.weather[0].main,
              description: element.weather[0].description,
              humidity: element.main.humidity,
              temp: Math.round(element.main.temp - 273),
              date: {
                dayOfWeek: this.getDayOfWeek(element.dt_txt),
                dayOfMonth: this.getDayOfMonth(element.dt_txt),
                month: this.getMonth(element.dt_txt),
              },
              wind: {
                speed: Math.round(element.wind.speed),
                degrees: element.wind.deg,
              },
              icon: element.weather[0].icon,
            });
          });
          this.setActualWeather(response);
          console.log(response);
        });
    },
    setActualWeather(response) {
      this.actualWeather = {
        main: response.data.list[0].weather[0].main,
        description: response.data.list[0].weather[0].description,
        humidity: response.data.list[0].main.humidity,
        temp: Math.round(response.data.list[0].main.temp - 273),
        date: {
          dayOfWeek: this.getDayOfWeek(response.data.list[0].dt_txt),
          dayOfMonth: this.getDayOfMonth(response.data.list[0].dt_txt),
          month: this.getMonth(response.data.list[0].dt_txt),
        },
        wind: {
          speed: Math.round(response.data.list[0].wind.speed),
          degrees: response.data.list[0].wind.deg,
        },
      };
    },
    getDayOfWeek(input) {
      let daysOfWeek = [
        "Sunday",
        "Monday",
        "Tuesday",
        "Wednesday",
        "Thursday",
        "Friday",
        "Saturday",
      ];
      let dt = new Date(input);
      return daysOfWeek[dt.getDay()];
    },
    getMonth(input) {
      let month = [
        "January",
        "February",
        "March",
        "April",
        "May",
        "June",
        "July",
        "August",
        "September",
        "October",
        "November",
        "December",
      ];
      let dt = new Date(input);
      return month[dt.getMonth()];
    },
    getDayOfMonth(input) {
      let dt = new Date(input);
      return dt.getDate();
    },
    getShortDayName(name) {
      return name.slice(0, 3);
    },
    getWeatherImage(icon) {
      let iconPath = "https://openweathermap.org/img/wn/" + icon + "@2x.png";
      return iconPath;
    },
    changeActualWeather(index) {
      this.actualWeather = this.weather[index];
    },
    chooseCity() {
      this.city = this.tempCity;
      this.fetchData(this.city);
    },
  },
};
</script>

